import { VehicleCard } from '@/components/VehicleCard';
import { mockVehicles } from '@/data/mockData';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import {
  Activity,
  AlertTriangle,
  Car,
  Ambulance,
  Helicopter,
  TrendingUp,
  Wrench,
  Bell,
  Clock,
  ArrowRight
} from 'lucide-react';
import Link from 'next/link';

export default function Home() {
  // Calculate dashboard statistics
  const totalVehicles = mockVehicles.length;
  const activeVehicles = mockVehicles.filter(v => v.status === 'active').length;
  const maintenanceVehicles = mockVehicles.filter(v => v.status === 'maintenance').length;
  const outOfServiceVehicles = mockVehicles.filter(v => v.status === 'out_of_service').length;

  const totalDevices = mockVehicles.reduce((acc, vehicle) => acc + vehicle.devices.length, 0);
  const overdueDevices = mockVehicles.reduce((acc, vehicle) =>
    acc + vehicle.devices.filter(d => d.status === 'overdue').length, 0
  );
  const dueSoonDevices = mockVehicles.reduce((acc, vehicle) =>
    acc + vehicle.devices.filter(d => d.status === 'due').length, 0
  );

  const cars = mockVehicles.filter(v => v.type === 'car');
  const ambulances = mockVehicles.filter(v => v.type === 'ambulance');
  const helicopters = mockVehicles.filter(v => v.type === 'helicopter');

  const priorityDevices = mockVehicles.flatMap(vehicle =>
    vehicle.devices.map(device => ({
      ...device,
      vehicleName: vehicle.name,
      vehicleId: vehicle.id,
      vehicleType: vehicle.type
    }))
  )
    .filter(device => device.status === 'overdue' || device.status === 'due')
    .sort((a, b) => {
      const statusPriority: Record<string, number> = {
        overdue: 0,
        due: 1,
        upcoming: 2,
        completed: 3
      };
      const criticalityPriority: Record<string, number> = { critical: 0, high: 1, medium: 2, low: 3 };

      const statusDiff = (statusPriority[a.status] ?? 3) - (statusPriority[b.status] ?? 3);
      if (statusDiff !== 0) return statusDiff;

      const criticalityDiff = criticalityPriority[a.criticality] - criticalityPriority[b.criticality];
      if (criticalityDiff !== 0) return criticalityDiff;

      return a.nextMaintenanceDate.getTime() - b.nextMaintenanceDate.getTime();
    })
    .slice(0, 6);

  return (
    <div className="min-h-screen fade-in">
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-white">Equipment Tracker Dashboard</h1>
          <p className="mt-1 text-sm text-slate-300">
            Monitor and manage maintenance for all hospital vehicles and equipment
          </p>
          <div className="mt-4 flex items-center gap-4">
            <Link href="/notifications">
              <Button variant="outline" size="sm">
                <Bell className="h-4 w-4 mr-2" />
                Notifications ({overdueDevices + dueSoonDevices})
              </Button>
            </Link>
            <Badge variant="secondary" className="px-3 py-1">
              <Activity className="h-4 w-4 mr-1" />
              System Active
            </Badge>
          </div>
        </div>

        {/* Hero Overview */}
        <div className="relative overflow-hidden rounded-3xl bg-gradient-to-br from-blue-600/60 via-cyan-500/40 to-emerald-500/40 text-white p-8 mb-8 soft-glow">
          <div className="relative z-10">
            <h2 className="text-2xl font-bold mb-2">Fleet Health Overview</h2>
            <p className="text-cyan-100 mb-6">
              Real-time status across all vehicles and equipment
            </p>
            <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
              <div className="bg-white/15 rounded-2xl p-4 border border-white/20">
                <div className="text-sm text-cyan-100">Active Vehicles</div>
                <div className="text-2xl font-bold">{activeVehicles}</div>
              </div>
              <div className="bg-white/15 rounded-2xl p-4 border border-white/20">
                <div className="text-sm text-cyan-100">Overdue Devices</div>
                <div className="text-2xl font-bold">{overdueDevices}</div>
              </div>
              <div className="bg-white/15 rounded-2xl p-4 border border-white/20">
                <div className="text-sm text-cyan-100">Due Soon</div>
                <div className="text-2xl font-bold">{dueSoonDevices}</div>
              </div>
            </div>
          </div>
          <div className="absolute -right-8 -top-8 h-32 w-32 rounded-full bg-white/10" />
          <div className="absolute -right-12 -bottom-12 h-40 w-40 rounded-full bg-white/5" />
        </div>
        {/* Statistics Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Total Vehicles</CardTitle>
              <Car className="h-4 w-4 text-slate-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-white">{totalVehicles}</div>
              <p className="text-xs text-slate-300">
                {cars.length} cars, {ambulances.length} ambulances, {helicopters.length} helicopters
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Active Vehicles</CardTitle>
              <TrendingUp className="h-4 w-4 text-emerald-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-emerald-200">{activeVehicles}</div>
              <p className="text-xs text-slate-300">
                {maintenanceVehicles} in maintenance, {outOfServiceVehicles} out of service
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Total Devices</CardTitle>
              <Wrench className="h-4 w-4 text-slate-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-white">{totalDevices}</div>
              <p className="text-xs text-slate-300">
                Across all vehicles and equipment
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-200">Maintenance Alerts</CardTitle>
              <AlertTriangle className="h-4 w-4 text-red-300" />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-red-200">{overdueDevices + dueSoonDevices}</div>
              <p className="text-xs text-slate-300">
                {overdueDevices} overdue, {dueSoonDevices} due soon
              </p>
            </CardContent>
          </Card>
        </div>

        {/* Priority Maintenance Queue */}
        <div className="mb-8">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl font-bold text-white">Priority Maintenance Queue</h2>
            <Link href="/vehicles" className="text-sm text-cyan-200 hover:text-cyan-100 flex items-center gap-1">
              View Fleet <ArrowRight className="h-4 w-4" />
            </Link>
          </div>

          {priorityDevices.length === 0 ? (
            <Card>
              <CardContent className="p-6 text-center text-slate-300">
                All equipment is up to date. No priority maintenance required.
              </CardContent>
            </Card>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {priorityDevices.map((device) => (
                <Card key={device.id} className="hover:shadow-md transition-shadow">
                  <CardContent className="p-6">
                    <div className="flex items-center justify-between mb-3">
                      <div className="font-semibold text-white">{device.name}</div>
                      <Badge className={device.status === 'overdue'
                        ? 'bg-red-500/20 text-red-200 border border-red-400/30'
                        : 'bg-amber-500/20 text-amber-200 border border-amber-400/30'}>
                        {device.status === 'overdue' ? 'Overdue' : 'Due Soon'}
                      </Badge>
                    </div>
                    <div className="text-sm text-slate-300 mb-2">{device.vehicleName}</div>
                    <div className="flex items-center gap-2 text-sm text-slate-400">
                      <Clock className="h-4 w-4" />
                      <span>Due: {device.nextMaintenanceDate.toLocaleDateString()}</span>
                    </div>
                    <div className="mt-4 flex items-center justify-between">
                      <Badge variant="secondary" className="text-xs">
                        {device.criticality} priority
                      </Badge>
                      <Link href={`/vehicle/${device.vehicleId}`}>
                        <Button size="sm" variant="outline">
                          View
                        </Button>
                      </Link>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </div>

        {/* Fleet Overview */}
        <div className="mb-8">
          <h2 className="text-2xl font-bold text-white mb-6">Fleet Overview</h2>

          {/* Cars Section */}
          <div className="mb-8">
            <div className="flex items-center gap-3 mb-4">
              <Car className="h-6 w-6 text-cyan-200" />
              <h3 className="text-xl font-semibold text-white">Charlie Cars ({cars.length})</h3>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5 gap-6">
              {cars.map((vehicle) => (
                <VehicleCard key={vehicle.id} vehicle={vehicle} />
              ))}
            </div>
          </div>

          {/* Ambulances Section */}
          <div className="mb-8">
            <div className="flex items-center gap-3 mb-4">
              <Ambulance className="h-6 w-6 text-emerald-200" />
              <h3 className="text-xl font-semibold text-white">Ford Ambulances ({ambulances.length})</h3>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5 gap-6">
              {ambulances.map((vehicle) => (
                <VehicleCard key={vehicle.id} vehicle={vehicle} />
              ))}
            </div>
          </div>

          {/* Helicopters Section */}
          <div className="mb-8">
            <div className="flex items-center gap-3 mb-4">
              <Helicopter className="h-6 w-6 text-blue-200" />
              <h3 className="text-xl font-semibold text-white">Medical Helicopters ({helicopters.length})</h3>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {helicopters.map((vehicle) => (
                <VehicleCard key={vehicle.id} vehicle={vehicle} />
              ))}
            </div>
          </div>
        </div>

        {/* Recent Activity */}
        <div>
          <h2 className="text-2xl font-bold text-white mb-6">Recent Maintenance Activity</h2>
          <Card>
            <CardContent className="p-6">
              <div className="text-center text-slate-300">
                <Wrench className="h-12 w-12 mx-auto mb-4 opacity-70" />
                <p className="text-lg font-medium text-white">Maintenance tracking in progress</p>
                <p className="text-sm">Full reports and analytics available in the Reports section.</p>
              </div>
            </CardContent>
          </Card>
        </div>
      </main>
    </div>
  );
}
